import { useState } from 'react'
import CharacterSetup from './CharacterSetup'
import GameBoard from './GameBoard'
import GameResult from './GameResult'
import './App.css'

const sampleCards = [
  // 
  { name: '', cost: 500, multiplier: 1.4, group: '', condition: '' },
  { name: '', cost: 500, multiplier: 1.3, group: '' },
  // 
  { name: '', cost: 500, multiplier: 1.4, group: '', condition: '' },
  { name: '', cost: 500, multiplier: 1.3, group: '', condition: '' },
  // 
  { name: '', cost: 500, multiplier: 2.0, group: '', condition: '' },
  { name: '', cost: 3000, multiplier: 2.2, group: '', condition: '' },
  { name: "Half,Yellow, Don't", cost: 2000, multiplier: 2.2, group: '', condition: '2000' },
  // 
  { name: '', cost: 1000, multiplier: 1.4, group: '', condition: '' },
  { name: '', cost: 200, multiplier: 1.4, group: '', condition: '' },
  // //
  { name: '//', cost: 1000, multiplier: 1.4, group: '' },
  { name: '', cost: 500, multiplier: 1.2, group: '' },
  // 
  { name: '', cost: 0, multiplier: 1.1, group: '', condition: '2' },
  { name: '', cost: 0, multiplier: 1.1, group: '', condition: '2' },
  { name: '', cost: 0, multiplier: 1.1, group: '', condition: '2' },
  { name: '', cost: 0, multiplier: 1.1, group: '', condition: '2' },
  { name: '', cost: 0, multiplier: 1.1, group: '', condition: '2' },
  // 
  { name: '', cost: 2000, multiplier: 2.0, group: '', condition: '2502' },
  // 
  { name: '', cost: 0, multiplier: 1.1, group: '', condition: '13030' },
  { name: 'Sit-up', cost: 0, multiplier: 1.2, group: '', condition: '13010' },
  // 
  { name: '', cost: 500, multiplier: 1.1, group: '', condition: '30' },
];

function App() {
  const [players, setPlayers] = useState(null)
  const [history, setHistory] = useState([])
  const [currentPlayer, setCurrentPlayer] = useState(0)
  const [gameOver, setGameOver] = useState(false)

  const handleStart = (playersData) => {
    setPlayers(playersData.map(p => ({ ...p, initialPower: p.power })));
    setHistory([])
    setCurrentPlayer(0)
    setGameOver(false)
  }

  const handleSelectCard = (card) => {
    setPlayers(prev => prev.map((p, idx) => {
      if (idx !== currentPlayer) return p
      if (card.condition && card.condition.includes('')) {
        const minMoney = parseInt(card.condition.replace(/[^0-9]/g, ''))
        if (p.money <= minMoney) return p
      }
      return {
        ...p,
        money: p.money - card.cost,
        power: Math.round(p.power * card.multiplier)
      }
    }))
    setHistory(prev => [
      ...prev,
      { playerIdx: currentPlayer, cardName: card.name, multiplier: card.multiplier, cost: card.cost }
    ])
  }

  // 
  const handleUnselectCard = (playerIdx, cardName) => {
    const card = sampleCards.find(c => c.name === cardName);
    if (!card) return;
    setPlayers(prev => prev.map((p, idx) => {
      if (idx !== playerIdx) return p;
      return {
        ...p,
        money: p.money + (card.cost || 0),
        power: Math.round(p.power / (card.multiplier || 1)),
      }
    }))
    setHistory(prev => {
      const idx = prev.map((h, i) => h.playerIdx === playerIdx && h.cardName === cardName ? i : -1).filter(i => i !== -1).pop();
      if (idx === undefined) return prev;
      return prev.slice(0, idx).concat(prev.slice(idx + 1));
    });
  }

  const handleGameOver = () => setGameOver(true)
  const handleRestart = () => setPlayers(null)

  if (!players) {
    return <CharacterSetup onStart={handleStart} />
  }

  if (gameOver) {
    return <GameResult players={players} onRestart={handleRestart} />
  }

  return (
    <div className="App">
      <h1> RPG </h1>
      <button onClick={handleGameOver} style={{ position: 'absolute', right: 24, top: 24, zIndex: 10 }}></button>
      <GameBoard
        players={players}
        cards={sampleCards}
        onSelectCard={handleSelectCard}
        history={history}
        currentPlayer={currentPlayer}
        onSelectPlayer={setCurrentPlayer}
        onUnselectCard={handleUnselectCard}
      />
    </div>
  )
}

export default App
